import { useState, useEffect, useCallback } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axiosInstance from "../../api/axiosConfig";
import Input from "../../components/form/input/InputField";

interface CategoryFormData {
  name: string;
  tag: string;
  icon: string;
}

export default function CategoryForm() {
  const navigate = useNavigate();
  const { categoryId } = useParams();
  const isEditMode = Boolean(categoryId);
  
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [formData, setFormData] = useState<CategoryFormData>({
    name: "",
    tag: "",
    icon: ""
  });

  const fetchCategoryDetails = useCallback(async () => {
    try {
      const response = await axiosInstance.get(`/admin/category/${categoryId}`);
      const categoryData = response.data.body.category;
      setFormData({
        name: categoryData.name,
        tag: categoryData.tag,
        icon: categoryData.icon
      });
    } catch (err: any) {
      setError(err.response?.data?.message || "Failed to fetch category details");
      console.error("Error fetching category:", err);
    }
  }, [categoryId]);

  useEffect(() => {
    if (isEditMode) {
      fetchCategoryDetails();
    }
  }, [isEditMode, fetchCategoryDetails]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      if (isEditMode) {
        await axiosInstance.put(`/admin/category/update/${categoryId}`, formData);
      } else {
        await axiosInstance.post("/admin/category/create", formData);
      }
      navigate("/categories");
    } catch (err: any) {
      setError(err.response?.data?.message || `Failed to ${isEditMode ? 'update' : 'create'} category`);
      console.error(`Error ${isEditMode ? 'updating' : 'creating'} category:`, err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-2xl mx-auto">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold">{isEditMode ? 'Edit Category' : 'Create New Category'}</h1>
          <button
            onClick={() => navigate("/categories")}
            className="bg-gray-500 text-white px-4 py-2 rounded-lg hover:bg-gray-600"
          >
            Cancel
          </button>
        </div>

        {error && (
          <div className="mb-4 p-4 bg-red-50 border border-red-200 text-red-600 rounded-md">
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6 bg-white shadow-md rounded-lg p-6">
          <div>
            <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-1">
              Name *
            </label>
            <Input
              id="name"
              name="name"
              type="text"
              value={formData.name}
              onChange={handleChange}
              placeholder="Enter category name"
              className="w-full"
            />
          </div>

          <div>
            <label htmlFor="tag" className="block text-sm font-medium text-gray-700 mb-1">
              Tag *
            </label>
            <Input
              id="tag"
              name="tag"
              type="text"
              value={formData.tag}
              onChange={handleChange}
              placeholder="Enter category tag (e.g. #newpairs)"
              className="w-full"
            />
          </div>

          <div>
            <label htmlFor="icon" className="block text-sm font-medium text-gray-700 mb-1">
              Icon *
            </label>
            <Input
              id="icon"
              name="icon"
              type="text"
              value={formData.icon}
              onChange={handleChange}
              placeholder="Enter icon name or URL"
              className="w-full"
            />
          </div>

          <div className="flex justify-end">
            <button
              type="submit"
              disabled={loading}
              className={`px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 
                ${loading ? 'opacity-50 cursor-not-allowed' : ''}`}
            >
              {loading ? (isEditMode ? 'Updating...' : 'Creating...') : (isEditMode ? 'Update Category' : 'Create Category')}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
} 